}

.spinner div {
  transform-origin: 40px 40px;
  animation: spinner 1.2s linear infinite;
}

.spinner div:after {
  content: " ";
  width: 6px;
  height: 18px;
  background: #ff6b01;
  border-radius: 20%;
  display: block;
  position: absolute;
  top: 3px;
  left: 37px;
}

.spinner div:nth-child(1) {
  animation-delay: -1.1s;
  transform: rotate(0deg);
}

.spinner div:nth-child(2) {
  animation-delay: -1s;
  transform: rotate(30deg);
}

.spinner div:nth-child(3) {
  animation-delay: -.9s;
