.header {
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 230px;
  padding-top: 44px;
  padding-bottom: 44px;
  margin: 0 auto;

  @include tablet {
    padding-top: 40px;
    padding-bottom: 60px;
  }

  @include desktop {
    padding-bottom: 44px;
  }

  &__library {
    opacity: 0.9;
    min-width: 320px;
    max-width: 767px;
    width: 100%;
    max-width: 480px;
    margin: 0 auto;
    background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.56),
        rgba(0, 0, 0, 0.56)
      ),
      url('../images/header-fone-library-mob-1x-min.jpg');
    background-position: center;

    @include retina {
      background-image: linear-gradient(
          to right,
          rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56)
        ),
        url('../images/header-fone-library-mob-2x-min.jpg');
    }

    @include tablet {
      background-image: linear-gradient(
          to right,
          rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56)
        ),
        url('../images/header-fone-library-tab-1x-min.jpg');
      max-width: 768px;

      @include retina {
        background-image: linear-gradient(
            to right,
            rgba(0, 0, 0, 0.56),
            rgba(0, 0, 0, 0.56)
          ),
          url('../images/header-fone-library-tab-2x-min.jpg');
      }
    }

    @include desktop {
      background-image: linear-gradient(
          to right,
          rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56)
        ),
        url('../images/header-fone-library-1x-min.jpg');
      max-width: 1280px;

      @include retina {
        background-image: linear-gradient(
            to right,
            rgba(0, 0, 0, 0.56),
            rgba(0, 0, 0, 0.56)
          ),
          url('../images/header-fone-library-2x-min.jpg');
      }
    }
  }

  &__menu {
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    display: flex;
    align-items: center;
    color: var(--primary-bg-color);
    max-height: 24px;

    &-wrap {
      display: flex;
      align-items: center;

      @include tablet {
        column-gap: 10px;
      }

      @include desktop {
        column-gap: 12x;
      }
    }
  }

  &__logo {
    /* margin-right: 112px; */
    margin-right: 61px;

    /* @include tablet {
			margin-right: 387px;
		}

		@include desktop {
			margin-right: 898px;
		} */

    @include tablet {
      margin-right: 326px;
    }

    @include desktop {
      margin-right: 837px;
    }

    &-wrap {
      @include tablet {
        display: flex;
        align-items: center;
        gap: 10px;
      }
    }

    &-name {
      display: none;

      @include tablet {
        display: block;
        font-size: 30px;
        line-height: 1.16;
        color: var(--primary-bg-color);
      }
    }
  }

  &__list {
    display: flex;
    gap: 20px;
    text-transform: uppercase;

    @include desktop {
      gap: 39px;
    }
  }
}

.current {
  &:after {
    content: '';
    display: block;
    width: 100%;
    height: 3px;
    background-color: var(--accent-menu-color);
  }
}

.filter {
  &__wrap {
    margin-top: 60px;
    display: flex;
    justify-content: center;

    @include tablet {
      margin-top: 40px;
    }
  }

  &__list {
    display: flex;
    gap: 20px;

    @include tablet {
      gap: 32px;
    }

    @include desktop {
      gap: 16px;
    }
  }

  &__button {
    height: 44px;
    width: 130px;

    @include tablet {
      width: 152;
    }

    @include desktop {
      height: 42px;
      width: 147px;
    }
  }

  &__text {
    text-transform: uppercase;
  }
}

.empty-library {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  text-align: center;

  color: var(--accent-color);
  font-weight: 500;
  max-width: 479px;
  // min-height: 420px;
  height: 100%;
  border-collapse: collapse;
  @include tablet {
    width: 768px;
    // min-height: 655px;
  }
  @include desktop {
    width: 1280px;
    // min-height: 780px;
  }
}
