.header {
  position: relative;
  opacity: 0.9;
  background-image: linear-gradient(
      to right,
      rgba(0, 0, 0, 0.56),
      rgba(0, 0, 0, 0.56)
    ),
    url('../images/header-fone-mob-1x-min.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  min-height: 230px;
  padding-top: 44px;
  padding-bottom: 44px;
  width: 100%;
  max-width: 480px;
  margin: 0 auto;

  @include retina {
    background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.56),
        rgba(0, 0, 0, 0.56)
      ),
      url('../images/header-fone-mob-2x-min.jpg');
  }

  @include tablet {
    padding-top: 40px;
    padding-bottom: 45px;
    max-width: 768px;
    background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.56),
        rgba(0, 0, 0, 0.56)
      ),
      url('../images/header-fone-tab-1x-min.jpg');
    opacity: 0.9;

    @include retina {
      background-image: linear-gradient(
          to right,
          rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56)
        ),
        url('../images/header-fone-tab-2x-min.jpg');
    }
  }

  @include desktop {
    padding-bottom: 44px;
    max-width: 1280px;
    opacity: 0.9;
    background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.56),
        rgba(0, 0, 0, 0.56)
      ),
      url('../images/header-fone-1x-min.jpg');

    @include retina {
      background-image: linear-gradient(
          to right,
          rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56)
        ),
        url('../images/header-fone-2x-min.jpg');
    }
  }

  &__menu {
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    display: flex;
    align-items: center;
    color: var(--primary-bg-color);
    max-height: 24px;
  }

  &__logo {
    margin-right: 61px;

    @include tablet {
      margin-right: 326px;
    }

    @include desktop {
      margin-right: 837px;
    }

    &-wrap {
      display: flex;
      align-items: center;

      @include tablet {
        column-gap: 10px;
      }

      @include desktop {
        column-gap: 12x;
      }
    }

    &-svg {
      fill: none;
      stroke: var(--primary-bg-color);
    }

    &-name {
      display: none;

      @include tablet {
        display: block;
        font-size: 30px;
        line-height: 1.16;
        color: var(--primary-bg-color);
      }
    }
  }

  &__list {
    display: flex;
    gap: 20px;
    text-transform: uppercase;
    margin-left: auto;

    @include desktop {
      gap: 39px;
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    row-gap: 20px;
    margin-top: 54px;
    position: relative;

    @include tablet {
      margin-top: 40px;
      justify-content: center;
      align-items: center;
    }

    @include desktop {
      margin-top: 41px;
    }

    &-wrap {
      display: flex;
      align-items: center;
      width: 100%;
      position: relative;

      @include tablet {
        max-width: 336px;
      }

      @include desktop {
        max-width: 393px;
      }

      &:after {
        content: '';
        width: 100%;
        height: 0.1px;
        background-color: var(--primary-bg-color);
        position: absolute;
        bottom: -4px;

        @include tablet {
          width: 336px;
        }

        @include desktop {
          width: 100%;
        }
      }
    }

    &-input {
      border: none;
      border-radius: 5px;
      padding-left: 3px;
      border-color: transparent;
      background: transparent;
      outline: none;
      height: 16px;
      width: 100%;
      color: var(--primary-bg-color);
      font-size: 14px;
      position: relative;
    }

    &-input::-webkit-input-placeholder {
      color: var(--primary-bg-color);
    }

    &-button {
      background-color: transparent;
      color: transparent;
      width: 30px;
      height: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      z-index: 10;
      position: absolute;
      right: -9px;
    }

    &-text {
      font-size: 14px;
      line-height: calc(16 / 14);
      color: var(--accent-menu-color);

      @include tablet {
        max-width: 393px;
        text-align: center;
      }
    }
  }

  &-text {
    font-size: 14px;
    line-height: calc(16 / 14);
    color: var(--accent-menu-color);
  }
}

.current {
  &:after {
    content: '';
    display: block;
    width: 100%;
    height: 3px;
    background-color: var(--accent-menu-color);
  }
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill {
  -webkit-box-shadow: inset 0 0 0 50px #10151d;
  /* колір фону */
  -webkit-text-fill-color: var(--primary-bg-color);
  /* колір тексту */
}

#btnSignOut {
  background-color: transparent;
}

#btnLibrary {
  cursor: pointer;
}
